.home-main{
  height: 100vh;
  background-image: linear-gradient(to top, #f731cc 0%, #453a94 100%);
  padding: 30px;
}


.home-container{
  height: 90vh;
  background-color: #fff;
  border-radius: 2rem;
}

.container-up{
  display: flex;
  justify-content: space-between;
  padding: 20px;
  align-items: center;

}

.container-up-1{
  display: flex;
  align-items: center;
  gap: 10px;
}
.container-up-1 img{
  height: 60px;
  border-radius: 15px;
}
.container-up-2{
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.container-up-2 h3{
  letter-spacing: 1px;
  font-family: 'Yatra One', cursive;
}
.container-up-1 h2{
  font-size: 32px;
  letter-spacing: 1px;
  font-family: 'Yatra One', cursive;
}

.container-up-2 a{
  color: #fff;
  text-decoration: none;
  padding: 10px 20px;
  background-color:var(--main-colour) ;
  border-radius: 40px;
}

.container-down{
  gap: 20px;
  padding: 20px;
  min-height: 75vh;
  display: grid;
  grid-template-columns: 4fr 1fr;
}

.container-down-right{
  width: 20vw;
  font-size: 12px;
}

.user-logo-con{
  display: flex;
  justify-content: center;
}
.container-down-right .user-logo-con img{
  border-radius: 10px;
  height: 100px;
}


.user-details{
  overflow-x:auto;
}

.custom-scroll::-webkit-scrollbar{
  height: 4px;
  width: 4px;
}
.custom-scroll::-webkit-scrollbar-thumb{
  background-color: #fff;
  border: 1px solid #757575;
  border-radius: 30px;
}

.custom-scroll::-webkit-scrollbar-thumb:hover{
  background-color: #777676;

}

.user-details h3{
  margin: 5px 0;
  color: #747474;
}

.user-details p{
  font-size: 20px;
  color: #221b1b;
}


.container-down-left-not-auth{
  border-radius: 10px;
  background-color: rgba(196, 192, 187, 0.342);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}


@media (max-width:768px) {
  .home-main{
    padding: 15px;
  }
  .container-up-1 h2{
    font-size: 15px;
  }
  .container-up-2 a{
    padding: 5px 10px; ;
    border-radius: 20px;
    font-size: 10px;
  }
  .container-up-2{
    flex-direction: column;
  }

  .container-down{
    min-height: 5vh;
    display: flex;
    flex-direction: column-reverse;
    gap: 0px;
    padding: 0 20px 20px;
  }

  .container-down-right{
    width: 80vw;
    font-size: 12px;
  }

  .user-details{
    margin-bottom: 10px;
  }

  .user-logo-con{
    display: none;
  }

  .user-sub{
    gap: 5px;
    display: flex;
  }

  .container-down-left-not-auth{
    margin-top: 10px;
    text-align: center;
    min-height: 60vh;
  }

}


