.container-down-left{
  height: 65vh;
  overflow: scroll;
  overflow-x: hidden;
  display: grid;
  align-items: flex-start;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 10px 0;
  border-radius: 10px;
  background-color: rgba(196, 192, 187, 0.342);
}

.board-head{
  height: 100%;
  margin: 0 10px;
  padding: 10px;
  border-radius: 10px;
  text-align: center;
  background-color: #cccccc;
}

.section-sub{
  height: 100%;
  border-radius: 10px;
}

.changeIt{
  background-color: #ffffff;
}


.card{
  padding: 10px;
  border: 3px solid #ffffff;
  background-color:#ffffff;
  transition: 300ms;
  border-radius: 10px;
  position: relative;
  height: 170px;
  width: 280px;
  margin-top: 10px;

}

.card:hover{
  border: 3px solid #26233a;
}

.card-title{
  margin-bottom: 10px;
}

.card-buttons{
  cursor: pointer;
  right: 10px;
  bottom: 10px;
  position: absolute;

}
.card-buttons img{
  cursor: pointer;
  transition: 200ms;
  padding: 5px;
  border: 1px solid rgb(49, 6, 99);
  margin-left: 10px;
  height: 30px;
  border-radius: 50%;
}
.card-buttons img:hover{
  background-color: #cacacab4;
}
.card-buttons img:active{
  scale: 0.9;
}

.card-description p{
  height: 100px;
  width: 240px;
  padding: 0 20px;
  color: gray;
  overflow: auto;
}

.card-buttons button{
  border: none;
  background-color: transparent;
}

@media (max-width:768px) {
  .container-down-left{
    padding-top: 70px;
    gap: 10px;
    grid-template-columns: 1fr;
    height: 60vh;
    }
    .card{
      padding: 10px;
      border: 3px solid #ffffff;
      background-color:#ffffff;
      transition: 300ms;
      border-radius: 10px;
      position: relative;
      height: 170px;
      width: 100%;
      margin-top: 10px;
    }

    .card-description p{
      width: 220px;

    }

}


