.add-card-con{
  
  width: 100%;
  padding: 10px;
  border: 3px solid #ffffff;
  background-color:#ffffff;
  transition: 300ms;
  border-radius: 10px;
}

.card-add{
  cursor: pointer;
  border-radius: 7px;
  transition: 300ms;
  border: 3px solid #a8a8a838;
  background-color:#a8a8a8a4;
  text-align: center;
}

.card-add:hover{
  border: 3px solid #414141a4;
}

.card-add:active{
  scale: 0.9;
}

.card-edit input{
  font-size:  15px ;
  background-color: rgba(223, 223, 223, 0.63);
  padding: 4px;
  outline: none;
  width: 100%;
  border: none;
  border-radius: 5px;
  margin-bottom: 10px;
}
.card-edit textarea{
  font-size:  15px ;
  background-color: rgba(223, 223, 223, 0.63);
  padding: 4px;
  outline: none;
  width: 100%;
  border: none;
  border-radius: 5px;
}


.card-edit-buttons{
  cursor: pointer;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-edit-buttons button{
  cursor: pointer;
  color: #fff;
  background-color: var(--main-colour);
  border: none;
  border-radius: 10px;
  padding: 7px 20px;
  
}

.card-edit-buttons img{
  transition: 200ms;
  padding: 5px;
  border: 1px solid rgb(49, 6, 99);
  margin-left: 10px;
  height: 30px;
  border-radius: 50%;
}
.card-edit-buttons img:hover{
  background-color: #cacacab4;
}
.card-edit-buttons img:active{
  scale: 0.9;
}

.add-card-upd{
  padding: 10px;
  position: absolute;
  z-index: 5;
  background-color: #fff;
  border-radius: 10px;
}


@media (max-width:768px) {
  .add-card-con{
    position: absolute;
    width: 80vw;
}
}




